label{
  color: red;
}
.login-div{
  min-height: 70vh; 
display: flex;
  justify-content: center;
  align-items: center;
  display: flex;
  flex-flow: column;
}
.login-form{
  border: 1px solid red;
  padding: 10px;
  border-radius: 10px;  
}
.pointer{
  cursor: pointer;
}
.order-popup{
  z-index: 9999;
  position: fixed;
  bottom: 0;  
  width: 100%;
  margin-bottom: 0px;
  background: brown;
}

.cart-table{
  margin-top: 60px;
  margin-bottom: 60px;
}
.order-summary{
  align-items: baseline;
}
.underline{
  text-decoration: underline;
}
.loader{
  position: fixed;
  left: 50%;
  top: 50%;
  z-index: 9999;
}
.production-col{
  min-height: 30rem;
}
.order-summary-border{
  border-bottom:1px solid;
}
